import React from 'react';
import Pomodoro from './Pomodoro';

class ClockApp extends React.Component {
  constructor(){
    super();
    this.state = {
      infoText:'session timer',
      workTime: 25,
      breakTime: 5,
      counting: false,
      working: true,
      timer: 25*60,
      referenceDate: 0, 
      pauseDate: 0,
      paused: true, 
      intervalID: 0
    }
  }

  switchTimer = ()=> {
    const audioEl = document.getElementById('beep');
    audioEl.play();
    const working = !this.state.working;
    const infoText = this.state.working ? "break timer" : "session timer";
    const timer = this.state.working ? this.state.breakTime * 60 : this.state.workTime * 60
    this.setState({
      working,
      timer,
      infoText
    })
  }

  componentDidMount(){
    this.setState({
      timer: 25 * 60,
      intervalID: setInterval(this.handleDate, 80)
    })
  }
  componentWillUnmount(){
   clearInterval(this.state.intervalID)
  }

  componentDidUpdate(){              
    if(this.state.timer <= 0){
      this.switchTimer();
    }
  }

  handleReset = ()=> {
    this.setState({
      infoText:'session timer',
      workTime: 25,
      breakTime: 5,
      counting: false,
      working: true,
      timer: 25*60,
      referenceDate: 0,
      pauseDate: 0,
      paused: true
    })
  }
  handleStartStop = ()=> {
    this.setState({
      counting: !this.state.counting,
      paused: !this.state.paused,
      pauseDate: (!this.state.paused || 0) && new Date()
    })
  }
  handleDate = ()=> {
    const newDate = new Date();
    if(!this.state.paused && newDate - this.state.referenceDate + this.state.pauseDate >= 1000){
      this.setState ({
        timer: this.state.timer - 1,
        referenceDate: new Date()
      })
    }
  }
  //for increment and decrement of timer values
  clickController = (event) => {
    const id = event.target.id;
    const working = this.state.working;
    switch(id){
      case 'session-increment':
        if (this.state.workTime >= 60){
          return;
        }
        this.setState({
          workTime: this.state.workTime + 1,
          timer: working ? this.state.timer + 60 : this.state.timer
        }) 
        break;
      case 'session-decrement':  
        if (this.state.workTime <= 1){
          return;
        }
        this.setState({
          workTime: this.state.workTime - 1,
          timer: working ? this.state.timer - 60 : this.state.timer
        })
        break;
      case 'break-increment':
        if (this.state.breakTime >= 60){
          return;
        }
        this.setState({
          breakTime: this.state.breakTime + 1,
          timer: !working ? this.state.timer + 60 : this.state.timer
        })
        break;
      case 'break-decrement':  
        if (this.state.breakTime <= 1){
          return;
        }
        this.setState({
          breakTime: this.state.breakTime - 1,
          timer: !working ? this.state.timer - 60 : this.state.timer
        })
        break;
      default: return;
    }  
  }

  render(){
    return (
      <div className="App">
        <audio id='beep' type='audio/mp3'>
          <source src="./audio/A-Tone-His_Self-1266414414.mp3"></source>
        </audio>
        <Pomodoro infoText={this.state.infoText} 
                  workTime={this.state.workTime} 
                  breakTime={this.state.breakTime}
                  counting={this.state.counting} 
                  timer={this.state.timer} 

                  clickController={this.clickController} 
                  onOff={this.handleStartStop}
                  reset={this.handleReset} 
                  />
      </div>
    );
  }

}

export default ClockApp;
