import React from 'react';

const DrumMachine = (props)=> {
    return (
        <div id="drum-machine">
            <h1>{props.text}</h1>
            <div id="display" onClick={props.click}>
                
                <div className="drum-pad" id="audio_1">
                    Q
                    <audio src="https://s3.amazonaws.com/freecodecamp/drums/Chord_1.mp3" id="Q" className="clip"></audio>
                </div>
                <div className="drum-pad" id="audio_2">
                    W
                    <audio src="https://s3.amazonaws.com/freecodecamp/drums/Chord_2.mp3" id="W" className="clip"></audio>
                </div>
                <div className="drum-pad" id="audio_3">
                    E
                    <audio src="https://s3.amazonaws.com/freecodecamp/drums/Chord_3.mp3" id="E" className="clip"></audio>
                </div>
                <div className="drum-pad" id="audio_4">
                    A
                    <audio src="https://s3.amazonaws.com/freecodecamp/drums/Give_us_a_light.mp3" id="A" className="clip"></audio>
                </div>
                <div className="drum-pad" id="audio_5">
                    S
                    <audio src="https://s3.amazonaws.com/freecodecamp/drums/Dry_Ohh.mp3" id="S" className="clip"></audio>
                </div>
                <div className="drum-pad" id="audio_6">
                    D
                    <audio src="https://s3.amazonaws.com/freecodecamp/drums/Bld_H1.mp3" id="D" className="clip"></audio>
                </div>
                <div className="drum-pad" id="audio_7">
                    Z
                    <audio src="https://s3.amazonaws.com/freecodecamp/drums/punchy_kick_1.mp3" id="Z" className="clip"></audio>
                </div>
                <div className="drum-pad" id="audio_8">
                    X
                    <audio src="https://s3.amazonaws.com/freecodecamp/drums/side_stick_1.mp3" id="X" className="clip"></audio>
                </div>
                <div className="drum-pad" id="audio_9">
                    C
                    <audio src="https://s3.amazonaws.com/freecodecamp/drums/Brk_Snr.mp3" id="C" className="clip"></audio>
                </div>

            </div>
            
        </div>
    )
}

export default DrumMachine;