import React from 'react';

const Sessions = (props)=> {
    let timeMins = Math.floor(props.timer/60);
    if(timeMins < 10){timeMins = '0' + timeMins}
    
    let timeSeconds = (props.timer % 60);
    if(timeSeconds < 10){timeSeconds = '0' + timeSeconds}

    return (
        <div id='session-control'>
            <p><b>Session Control</b></p>
            <div>
                <button id='start_stop' className='btnControls' onClick={props.onOff}>start / stop</button>  
                <button id='reset' className='btnControls' onClick={props.reset}>reset</button>
            </div>

            <p id='timer-label'>{props.infoText}</p>
            <p id='time-left'>time left: {` ${timeMins}:${timeSeconds}`}</p>

        </div>
    );
}

export default Sessions;