import React from 'react';

const Calculator = (props)=> {
    return (
        <div>
            
            <div id="calcGrid" onClick={props.clickController}>
                <div id="input" className="gridItem noHiLite">{props.input || 0}</div>
                <div id="display_calc" className="gridItem noHilite">{props.output || 0}</div>
                <div id="equals" className="gridItem">=</div>
                
                <div id="zero" className="gridItem">0</div>
                <div id="one" className="gridItem">1</div>
                <div id="two" className="gridItem">2</div>
                <div id="three" className="gridItem">3</div>
                <div id="four" className="gridItem">4</div>
                <div id="five" className="gridItem">5</div>
                <div id="six" className="gridItem">6</div>
                <div id="seven" className="gridItem">7</div>
                <div id="eight" className="gridItem">8</div>
                <div id="nine" className="gridItem">9</div>

                <div id="add" className="gridItem">+</div>
                <div id="subtract" className="gridItem">-</div>
                <div id="multiply" className="gridItem">*</div>
                <div id="divide" className="gridItem">/</div>

                <div id="clear" className="gridItem">clear</div>
                <div id="decimal" className="gridItem">.</div>

            </div>
        </div>
    )
}

export default Calculator;