import React from 'react';

//module for parse
const marked = require('marked');
//return key as break
marked.setOptions({
  breaks: true
})
const renderer = new marked.Renderer();


const TextBox = (props)=> {
    return (
        <div>
            <h1 id="editHeading">Editor</h1>
            {/* input area */}
            <textarea 
            type="text" 
            id="editor" 
            value={props.text}
            onChange={props.onChange}>
            </textarea>

            <h1 id="outputHeading">Text Output</h1>
            {/* formatted */}
            <div id="preview" dangerouslySetInnerHTML={{__html: marked(props.text, {renderer: renderer} )}}>
            </div>

        </div>
    );
}

export default TextBox;