import React, {useState} from 'react';
import TextBox from './TextBox';

const placeholder = 
`# some h1 size text
## some h2 size text
**bold text**

> This is a block quote.

Magic inline html tags \`<p></p> \`
\`\`\` 
const myFunction = ()=> {Do All the Things}
\`\`\`

This is a [link to FreeCodeCamp](https://www.freecodecamp.com)

- Unordered List 
  - Indented
    - Hi
1. ordering
2. of 
3. the things

![React Logo w/ Text](https://goo.gl/Umyytc)
`;

const TextApp = () => {
  const [text, setText] = useState(placeholder);

  const handleChange = (event) => {
    const {value} = event.target;
    setText(value)
  }
 
  return (
    <div className="App">
      <TextBox onChange={handleChange} text={text}/>
    </div>
  );
}

export default TextApp;