import React from 'react';

const Work = (props)=> {
    return (
        <div id="session-label">
            <p><b>Session Length</b></p>
            
            <p id="session-length">{props.workTime}</p>
            <div>
                <button id="session-decrement" className="btnTime" onClick={props.clickController}>-</button>
                <button id="session-increment" className="btnTime" onClick={props.clickController}>+</button>
            </div>
            
        </div>
    );
}

export default Work;