import React from 'react';
import { Route, Switch, Link } from 'react-router-dom';
import './App.css';

import QuoteApp from './components/quoteMachine/QuoteApp';
import CalcApp from './components/calculator/CalcApp';
import TextApp from './components/markDownPreviewer/TextApp';
import DrumApp from './components/drumMachine/DrumApp';
import ClockApp from './components/pomodoroClock/ClockApp';


function App() {
  return (
    <div className="App">
      
      <div className="nav">
        <ul>
          <li><Link to ="/">Quote Machine</Link></li>
          <li><Link to ="/drumMachine">Drum Machine</Link></li>
          <li><Link to ="/markdown">Markdown Previewer</Link></li>
          <li><Link to ="/pomodoro">Pomodoro Clock</Link></li>
          <li className="last"><Link to ="/calculator">Calculator</Link></li>
        </ul>
      </div>

      <Switch>
        <Route exact path='/'><QuoteApp /></Route>
        <Route path='/drumMachine'><DrumApp /></Route>
        <Route path='/markdown'><TextApp /></Route>
        <Route path='/pomodoro'><ClockApp /></Route>
        <Route path='/calculator'><CalcApp /></Route>
        <Route component={ () => <div>404 - Page not Found</div> } />
      </Switch>
    </div>
  );
}

export default App;
