import React, {useState} from 'react';
import Calculator from './Calculator';
import {evaluate} from 'mathjs';

const CalcApp = () => {
  const [inputDisplay, setInputDisplay] = useState('0');
  const [currentNumber, setCurrentNumber] = useState('0');
  const [formulaEntered, setFormulaEntered] = useState('0');

  const handleInput = (event) => {  
    //TODO: use props for key values instead.
    /*key text values are extracted, but only specific vals added to string for later evaluation*/
    const val = event.target.innerText;  
  
    switch(val){  
      //reset all values with clear
      case 'clear':
          setInputDisplay('0');
          setCurrentNumber('0');
          setFormulaEntered('');
          break;
      //add decimal if currentNumber does not already contain a decimal
      case '.':
        if(currentNumber.indexOf('.') < 0){
          setCurrentNumber(currentNumber + val);
          setInputDisplay(inputDisplay + val);
        }
        break;
      //add 0 only if input string is not 0.   
      case '0':
        if (inputDisplay !== '0') {
          setCurrentNumber(currentNumber + val);
          setInputDisplay(inputDisplay + val);
        }
        break;
      case '1':
      case '2':
      case '3':
      case '4':
      case '5':
      case '6':
      case '7':
      case '8':
      case '9':
        //do not allow a leading 0.  todo: limit number length
        if (inputDisplay !== '0'){
          setCurrentNumber(currentNumber + val);
          setInputDisplay(inputDisplay + val);
        } 
        else {
          setCurrentNumber(val)
          setInputDisplay(val)
        }
        break;
      case '+':
      case '-':
      case '*':
      case '/':
        //currentNumber is empty only if prior key was a number (initial state is 0).
        if(currentNumber !== ''){
          setCurrentNumber('');  //clear currentNumber entry
          setInputDisplay(inputDisplay + val);
        }
        //replace last operator.
        else{
            let inputCopy = [...inputDisplay];
            inputCopy.pop();    
            inputCopy.join(''); 
            setInputDisplay(inputCopy + val);
          }
        break;
      case '=':
        //set input to evaluated value and reset currentNumber to 0.
        let formulaEntered = inputDisplay;
        let result = evaluate(inputDisplay).toString();
        setCurrentNumber('0');
        setFormulaEntered(formulaEntered);
        setInputDisplay(result);
        break;   
      default: return;  
    }
  }

  return (
    <div className="App">
        <Calculator input={formulaEntered} output={inputDisplay} clickController={handleInput} />
    </div>
  );
}

export default CalcApp;