import React, {useState, useEffect, useCallback} from 'react';
import DrumMachine from './DrumMachine';

const DrumApp = () => {
  const [keyInput, setKeyInput] = useState('Click or press keys to play.');

  const handleKeyEvents = useCallback(event => {
    const val = event.key;
    playSound(val);
    setKeyInput(val);
  }, []);

  useEffect(() => {
    document.addEventListener('keydown', handleKeyEvents);
    return () => {document.removeEventListener('keydown', handleKeyEvents);}
  }, [handleKeyEvents]);  //it's like one of those nested russian dolls. yes.

  const handleClick = (event) => {
    const val = event.target.innerText;
    playSound(val);
    setKeyInput(val)
  }

  const playSound = (key) => {
    let keyInput = key.toUpperCase();
    let sound = document.getElementById(keyInput);
    if (sound){
      sound.currentTime = 0;
      sound.play()
    }
  }
  
  return (
    <div className="App">
      <DrumMachine text={keyInput} 
      click={handleClick}/>
    </div>
  );
}

export default DrumApp;