import React from 'react';

const Play = (props)=> {
    return (
        <div id="break-label">
            <p><b>Break Length</b></p>
            
            <p id="break-length">{props.breakTime}</p>
            <div>
                <button id="break-decrement" className="btnTime" onClick={props.clickController}>-</button>
                <button id="break-increment" className="btnTime" onClick={props.clickController}>+</button>
            </div>
            
        </div>
    );
}

export default Play;